@import "./light.scss";
@import "./dark.scss";
#mainVS,
#mainGov,
#mainVsv,
#mainPrs {
  display: flex;
  height: 768px;
  fieldset {
    border: none;
    margin-top: 7px;
    padding-left: 12px;
  }
}
#editMaint,
#prsEditMaint {
  width: 98%;
  height: 90%;
}
h2 {
  color: #000;
}
* {
  margin: 0;
  padding: 0;
}
.Dropdown-disabled,
input:disabled {
  cursor: default;
  background: #ccc;
  border-color: rgba(118, 118, 118, 0.3);
}
@media print {
  body * {
    visibility: hidden;
  }
  #myGrid,
  #myGrid * {
    visibility: visible;
  }
  #myGrid {
    position: absolute;
    left: 0;
    top: 0;
  }
}
.ag-theme-alpine .ag-cell,
.ag-theme-alpine .ag-full-width-row .ag-cell-wrapper.ag-row-group {
  padding-left: 0px;
}
.ag-theme-alpine .ag-header-cell,
.ag-theme-alpine .ag-header-group-cell {
  padding-left: 0px;
}
.ag-theme-alpine .ag-root-wrapper {
  border: none !important;
}
.ag-theme-alpine .ag-row:nth-of-type(odd) {
  background-color: #f3f3f3;
}
button {
  display: inline-block;
  padding: 0.3em 1.2em;
  border: 0.1em solid #ffffff;
  margin: 0 0.3em 0.3em 0;
  border-radius: 0.12em;
  box-sizing: border-box;
  text-decoration: none;
  font-weight: 300;
  color: black;
  text-align: center;
  transition: all 0.2s;
}

.primary {
  background: $btn_primary !important;
  color: $white !important;
  border-radius: 16px !important;
  border: none !important;
  padding: 0px 12px !important;
}
.secondary {
  color: $btn_primary !important;
  border: none !important;
  padding: 8px !important;
  background-color: #2199f300 !important;
}
